import React, {useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, Button, Container, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message/Message'
import Loader from '../components/Loader/Loader'
import { listProductDetails, updateProduct } from '../redux/product/product.action'
import ProductActionTypes from '../redux/product/product.types'

const ProductEditView = ({ match, history }) => {

    const productId = match.params.id

    const [name, setName] = useState('')
    const [price, setPrice] = useState(0)
    const [image, setImage] = useState('')
    const [brand, setBrand] = useState('')
    const [category, setCategory] = useState('')
    const [countInStock, setCountInStock] = useState(0)
    const [description, setDescription] = useState('')

    const dispatch = useDispatch()

    const productDetails = useSelector(state => state.productDetails)
    const { loading, error, product } = productDetails

    const productUpdate = useSelector(state => state.productUpdate)
    const { loading: loadingUpdate, error: errorUpdate, success: successUpdate } = productUpdate
    
    useEffect(() => {
        if (product) {
            if (successUpdate) {
                dispatch({ type: ProductActionTypes.PRODUCT_UPDATE_RESET})
                history.push('/admin/productlist')
            } else {
                if (!product.name || product._id !== productId) {
                    dispatch(listProductDetails(productId))
                } else {
                    setName(product.name)
                    setPrice(product.price)
                    setImage(product.image)
                    setBrand(product.brand)
                    setCategory(product.category)
                    setCountInStock(product.countInStock)
                    setDescription(product.description)
                }
            }
        }  
    }, [dispatch, history, productId, product, successUpdate])

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(updateProduct({
            _id: productId,
            name,
            price,
            image,
            brand,
            category, 
            description,
            countInStock
        }))
    }
    
    return (
        <>
            <Link to='/admin/productlist' className='btn btn-light my-3'>
                Go Back
            </Link>
            <Container>
                <Row className='justify-content-md-center'>
                    <Col xs={10} md={6}>
                    <h1>Edit Product</h1>
                    {loadingUpdate && <Loader />}
                    {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
                    {loading ? <Loader /> : error ? (
                        <Message variant='danger'>{error}</Message>
                    ) : (
                        <Form onSubmit={submitHandler}>
                            <Form.Group controlId='name'>
                                <Form.Label>Name</Form.Label>
                                <Form.Control 
                                    type='text' 
                                    placeholder='Enter name' 
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}></Form.Control>
                            </Form.Group>
                            <Form.Group controlId='price'>
                                <Form.Label>Price</Form.Label>
                                <Form.Control 
                                    type='number' 
                                    placeholder='Enter price' 
                                    value={price}
                                    onChange={(e) => setPrice(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Form.Group controlId='image'>
                                <Form.Label>Image</Form.Label>
                                <Form.Control 
                                    type='text' 
                                    placeholder='Enter image url' 
                                    value={image}
                                    onChange={(e) => setImage(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Form.Group controlId='brand'>
                                <Form.Label>Brand</Form.Label>
                                <Form.Control 
                                    type='text' 
                                    placeholder='Enter brand' 
                                    value={brand}
                                    onChange={(e) => setBrand(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Form.Group controlId='category'>
                                <Form.Label>Category</Form.Label>
                                <Form.Control 
                                    type='text' 
                                    placeholder='Enter category' 
                                    value={category}
                                    onChange={(e) => setCategory(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Form.Group controlId='countInStock'>
                                <Form.Label>Count In Stock</Form.Label>
                                <Form.Control 
                                    type='number' 
                                    placeholder='Enter countInStock' 
                                    value={countInStock}
                                    onChange={(e) => setCountInStock(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Form.Group controlId='description'>
                                <Form.Label>Description</Form.Label>
                                <Form.Control 
                                    type='text' 
                                    placeholder='Enter description' 
                                    value={description}
                                    onChange={(e) => setDescription(e.target.value)}></Form.Control>
                            </Form.Group>

                            <Button type='submit' variant='primary'>Update</Button>
                        </Form>  
                    )}   
                    </Col>
                </Row>            
            </Container>
        </>        
    )
}

export default ProductEditView